html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

a {
  text-decoration: none;
}

img, figure {
  display: block;
}

video {
  width: 100%;
}

html,
body {
  height: 100%;
}

@font-face {
  font-family: "Guard-bold";
  src: url(../fonts/GuardianEgyptian-Bold.eot);
  src: url(../fonts/GuardianEgyptian-Bold.woff);
  src: url(../fonts/GuardianEgyptian-Bold.ttf);
  font-style: normal;
  font-weight: bold;
}

@font-face {
  font-family: "Guard-reg";
  src: url(../fonts/GuardianEgyptian-Regular.eot);
  src: url(../fonts/GuardianEgyptian-Regular.woff);
  src: url(../fonts/GuardianEgyptian-Regular.ttf);
  font-style: normal;
  font-weight: bold;
}

.wrap {
  background: #f6f6f6;
  min-height: 100%;
}

.container {
  max-width: 850px;
  padding: 0 10px;
  margin: 0 auto;
}

.article {
  padding-top: 48px;
}

.article__title {
  font-family: "Helvetica";
  font-weight: 700;
  font-size: 15px;
  color: #767676;
  text-transform: uppercase;
}

.article__info {
  display: flex;
  align-items: center;
  padding: 20px 0px;
}

.article__info-text {
  font-family: "Guard-bold";
  padding-right: 36px;
  color: #121212;
  font-size: 48px;
}

@media only screen and (max-width: 474px) {
  .article__info-text {
    font-size: 30px;
  }
}

.list__item {
  font-family: "Guard-reg";
  border-radius: 6px;
  background-color: #ffffff;
  margin-top: 10px;
  cursor: pointer;
  line-height: 24px;
}

.list__item--is-close .list__more {
  visibility: hidden;
  opacity: 0;
  height: 0;
  animation: is-close 1s ease 0s;
}

.list__item--is-close .list__title:after {
  transform: rotate(135deg);
  transition: transform 1s ease 0s;
}

.list__item--is-open .list__more {
  visibility: visible;
  opacity: 1;
  animation: is-open 1s ease 0s;
  padding-bottom: 15px;
}

.list__item--is-open .list__more:before {
  animation: line-open 1.5s ease forwards 0.5s;
}

.list__item--is-open .list__title:after {
  transform: rotate(-45deg);
  transition: transform 1s ease 0s;
}

.list__title {
  position: relative;
  padding: 10px 75px 12px 26px;
  color: #121212;
  font-size: 18px;
}

.list__title:after {
  display: block;
  content: '';
  position: absolute;
  top: 40%;
  right: 40px;
  width: 8px;
  height: 8px;
  border-right: 2px solid #767676;
  border-top: 2px solid #767676;
}

.list__more {
  font-size: 15px;
}

.list__more:before {
  content: '';
  display: block;
  width: 0%;
  height: 2px;
  background: #c70000;
}

.list__desc {
  padding: 13px 75px 20px 26px;
  height: 115px;
  overflow: hidden;
}

.list__link {
  padding-left: 26px;
  color: #c70000;
}

.list__link:hover {
  text-decoration: underline;
}

.btn {
  font-family: "Helvetica";
  font-weight: 700;
  padding: 10px 0;
  width: 143px;
  border-radius: 6px;
  background: #c70000;
  font-size: 13px;
  color: #ffffff;
  text-transform: uppercase;
}

.btn:hover {
  background: rgba(199, 0, 0, 0.7);
}

.page {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 40px 0 67px;
}

.page__btn {
  opacity: 1;
  visibility: visible;
  transition: all 0.2s linear;
}

@media only screen and (max-width: 448px) {
  .page__btn {
    width: 30%;
  }
}

@media only screen and (max-width: 380px) {
  .page__btn {
    width: 20%;
  }
}

.page__btn--is-hidden {
  opacity: 0;
  visibility: hidden;
  transition: all 0.2s linear;
}

.page__number-inner {
  font-family: "Helvetica";
  font-size: 13px;
  font-weight: 700;
}

.page__input {
  width: 33px;
  padding: 6px 0px 6px 6px;
  border-radius: 6px;
  color: #c70000;
}

.page__number {
  color: #767676;
  padding-left: 6px;
}

@keyframes is-open {
  0% {
    opacity: 0;
    height: 0;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
    height: 155px;
  }
}

@keyframes is-close {
  0% {
    opacity: 1;
    height: 155px;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    height: 0;
  }
}

@keyframes line-open {
  0% {
    width: 0;
  }
  100% {
    width: 100%;
  }
}
